import React from "react";
import PortfolioComponent from "./CategoryComponent";

import beer from "../image/beer1.PNG";
import clm from "../image/clm1.PNG";
import flex from "../image/flexmember.PNG";
import chill from "../image/chillcrib.PNG";
import FooterName from "./footer";
import Fade from "react-reveal/Fade";

import { Link } from "react-router-dom";

class Category extends React.Component {
  render() {
    return (
      <div className="container">
        <div
          style={{
            fontSize: "40px",
            textAlign: "center",
            padding: "40px",
            color: "Gray"
          }}
        >
          Portfolio
        </div>

        <div class="ui link cards">
          <Fade bottom>
            <Link to="/BeerStore">
              <PortfolioComponent
                projectname="Beer Store"
                technologies="Ajax, JSON, Google-Map Api, Bootstrap, HTML, JavaScript, CSS, MYSQL"
                images={beer}
                desc="Team project from College"
                date="October 2018"
              />
            </Link>
          </Fade>

          <Fade bottom>
            <Link to="/ClmBookingSystem">
              <PortfolioComponent
                projectname="Appointment System"
                technologies="C#, ASP.NET MVC,Entity Framework, Razor, Bootstrap, JavaScript, CSS, SQL"
                images={clm}
                desc="This is a personal project Booking Appointment System.."
                date="April 2019"
              />
            </Link>
          </Fade>

          <Fade bottom>
            <Link to="/FlexiLearn">
              <PortfolioComponent
                projectname="FlexiLearn"
                technologies="C#, ASP.NET MVC, Entity Framework, Razor, Bootstrap, JavaScript, CSS, SQL"
                images={flex}
                desc="Project for memebers register for courses e.g Udemy or Lynda"
                date="October 2018"
              />
            </Link>
          </Fade>

          <Fade bottom>
            <Link to="/">
              <PortfolioComponent
                projectname="ChillCrib"
                technologies="PHP, Bootstrap, HTML, JavaScript, CSS, MYSQL"
                images={chill}
                desc="Personal Project: Put on hold for reconstruction"
                date="UNDER CONSTRUCTION"
              />
            </Link>
          </Fade>
        </div>
        <FooterName />
      </div>
    );
  }
}
export default Category;
